<template>

    <v-card
    height="525"
    class="mx-auto rounded-10"
    max-width="400"
    elevation="5"
    >
        <v-dialog
            v-model="imgDialog"
            fullscreen
            transition="scale-transition"
        >
            <v-card class="overlay-card">
                <v-row class="ma-0 pa-0" justify="end">
                    <v-btn icon x-large @click="imgDialog=false" dark>
                        <v-icon>
                            clear
                        </v-icon>
                    </v-btn>
                </v-row>
                <v-carousel
                    class="pt-5"
                    height="90vh"
                >
                    <v-carousel-item
                    v-for="(screen, i) in app.screens"
                    :key="i"
                    
                    >
                        <div align="center">
                            <v-img :src="screen" class="hola" contain>
                                <template v-slot:placeholder>
                                    <v-row
                                    class="fill-height ma-0"
                                    align="center"
                                    justify="center"
                                    >
                                    <v-progress-circular indeterminate color="white"></v-progress-circular>
                                    </v-row>
                                </template>
                            </v-img>
                        </div>
                    </v-carousel-item>
                </v-carousel>
            </v-card>
        </v-dialog>
        <v-carousel
            class="pt-5"
            cycle
            height="200"
            show-arrows-on-hover
            hide-delimiters
            progress
            :progress-color="this.getColor()"
        >
            <v-tooltip bottom>
                <template v-slot:activator="{on}">
                    <v-btn icon class="see-screens"
                    v-on="on"
                    @click="imgClicked()"
                    :light="$vuetify.theme.dark?false:true"
                    >
                        <v-icon>
                            collections
                        </v-icon>
                    </v-btn>
                </template>
                <span>
                    Ver imágenes
                </span>
            </v-tooltip>
            
            <v-carousel-item
            v-for="(screen, i) in app.screens"
            :key="i"
            >
                <div align="center">
                    <v-img :src="screen" class="carousel-imgs" contain>
                        <template v-slot:placeholder>
                            <v-row
                            class="fill-height ma-0"
                            align="center"
                            justify="center"
                            >
                            <v-progress-circular indeterminate></v-progress-circular>
                            </v-row>
                        </template>
                    </v-img>
                </div>
            </v-carousel-item>
        </v-carousel>
        <v-card-title>{{app.softwareName}}</v-card-title>
        <v-card-text>
            {{app.description}}
            <div v-if="app.externalURL!=null">
                <a :href="app.externalURL" target="Starfall">{{app.hyperlinkText}}</a>
            </div>
        </v-card-text>
        <v-card-actions>
            <v-menu
                :close-on-content-click="false"
                :nudge-width="200"
                max-width="250px"
                absolute
                v-model="developmentInfoPopup"
                >
                <template v-slot:activator="{ on }">
                    <v-row class="pa-0 ma-0" justify="center">
                        <v-btn 
                        small 
                        :color="getColor()"
                        v-on="on"
                        :light="$vuetify.theme.dark" 
                        :dark="!$vuetify.theme.dark"
                        >
                        Ver info. de desarrollo
                        </v-btn>
                    </v-row>
                </template>

                <v-card :color="this.getColor()" 
                :light="$vuetify.theme.dark" 
            :dark="!$vuetify.theme.dark"
                >
                    <v-row class="pa-0 ma-0">
                            <v-col class="pa-0 ma-0" cols="10">
                                <v-card-title class="pb-0 pr-0">
                                    {{app.softwareName}}
                                </v-card-title>
                            </v-col>
                            <v-col class="pa-0 ma-0" cols="2">
                                <v-btn icon large @click="developmentInfoPopup=false">
                                    <v-icon>
                                        clear
                                    </v-icon>
                                </v-btn>
                            </v-col>
                        </v-row>

                    <v-card-text class="pb-0 pt-0">
                        <v-row class="pa-0 ma-0">
                            <v-col class="pa-0">
                                <p class="title ma-0">Front-end</p>
                            </v-col>
                            <v-col class="pa-0">
                                <p class="title ma-0">Back-end</p>
                            </v-col>
                        </v-row>
                    </v-card-text>
                    <v-row class="pa-0 ma-0">
                        <v-col cols="6" class="pa-0 ma-0">
                            <v-list-item>
                                <v-list-item-content>
                                    <v-list-item-title>Framework</v-list-item-title>
                                    <v-list-item-subtitle class="text-wrap">{{app.frontendFramework}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-list-item>
                            <v-list-item>
                                <v-list-item-content>
                                    <v-list-item-title>Lenguaje</v-list-item-title>
                                    <v-list-item-subtitle class="text-wrap">{{app.frontendLanguage==null?'Ninguno':app.frontendLanguage}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-list-item>
                        </v-col>
                        <v-col cols="6" class="pa-0 ma-0">
                            <v-list-item>
                                <v-list-item-content>
                                    <v-list-item-title>Framework</v-list-item-title>
                                    <v-list-item-subtitle class="text-wrap">{{app.backendFramework==null?'Ninguno':app.backendFramework}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-list-item>
                            <v-list-item>
                                <v-list-item-content>
                                    <v-list-item-title>Lenguaje</v-list-item-title>
                                    <v-list-item-subtitle class="text-wrap">{{app.backendLanguage==null?'Ninguno':app.backendLanguage}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-list-item>
                            <v-list-item>
                                <v-list-item-content>
                                    <v-list-item-title>Database</v-list-item-title>
                                    <v-list-item-subtitle class="text-wrap">{{app.backendDB==null?'Ninguno':app.backendDB}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-list-item>
                        </v-col>
                    </v-row>
                </v-card>
            </v-menu>
        </v-card-actions>
        <br>
    </v-card>
</template>
<script>
import { getters, mutations } from '../../store/store';
export default {
    props:{
        app:Object
    },
    data:()=>({
        selectedImg:String,
        imgDialog:false,
        developmentInfoPopup:false
    }),
    methods:{
        imgClicked(){
            this.imgDialog=true;
        },
        setColor:mutations.setAccentColor,
        getColor:getters.accentColor
    }
}
</script>
<style scoped>
    .carousel-imgs {
        height: 150px;
    }
    .hola{
        height: 85vh;
    }
    .overlay-card{
        background-color: rgba(0, 0, 0, 0.8)
    }
    .see-screens{
        position:absolute;
        z-index: 1;
        bottom: 30px;
        right: 5px;
    }
</style>